import React, { Component } from 'react'
import { Div } from 'glamorous'
import Headline from './Headline'
import PropTypes from 'prop-types'
import FocusTrap from 'react-focus-trap'
import disableScrolling from '../HOC/disableScrolling'
import { stylesShape } from '../../intl/shapes'

class DialogWindow extends Component {
  static propTypes = {
    text: PropTypes.string.isRequired,
    picUrl: PropTypes.string.isRequired,
    picAlt: PropTypes.string.isRequired,
    css: stylesShape
  }

  wrapperStyles = {
    position: 'absolute',
    zIndex: '1000',
    width: '100%',
    height: '100%',
    backgroundColor: 'rgba(140, 152, 165, 0.5)',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    '@media(max-width: 540px)': {
      backgroundColor: 'rgba(89, 101, 114, 0.9)'
    }
  }

  modalStyles = {
    backgroundColor: '#ffffff',
    width: '445px',
    borderRadius: '5px',
    boxShadow: '-1px 0px 13px 1px rgba(41, 41, 42, 0.3)',
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    padding: '35px 0px',
    '@media(max-width: 540px)': {
      width: '100%'
    }
  }

  headlineStyles = {
    fontSize: '24px',
    textAlign: 'center'
  }

  render() {
    const { text, children, picUrl, picAlt, css } = this.props
    
    return (
      <FocusTrap>
        <Div css={this.wrapperStyles}>
          <Div css={this.modalStyles}>
            <img src={picUrl} alt={picAlt} />
            <Headline css={this.headlineStyles}>{text}</Headline>
            <Div css={css}>
              {children}
            </Div>
          </Div>
        </Div>
      </FocusTrap>
    )
  }
}

export default disableScrolling(DialogWindow)


