import glamorous from 'glamorous';
import React, { Component } from 'react'
import PropTypes from 'prop-types'

const GreyBtnComponent = glamorous.button({
  padding: '0px 15px',
  height: '40px',
  borderRadius: '3px',
  backgroundColor: '#e9ecef',
  color: '#000000',
  fontFamily: 'TextBook, sans-serif',
  fontWeight: 'normal',
  border: 'none',
  fontSize: '16px',
  cursor: 'pointer',
  transition: '.15s ease-in-out',
  outline: 'none',
  ':hover,:focus': {
    backgroundColor: '#dde0e4'
  },
  ':disabled': {
    backgroundColor: '#e1e5e8',
    color: '#c8cbcf'
  },
  ':active': {
    backgroundColor: '#c8cdd3'
  },
  '@media(max-width: 540px)': {
    padding: '0px 20px'
  }
})

export default class GreyBtn extends Component {
  render() {
    const { text, ...allProps } = this.props
    return (
      <GreyBtnComponent {...allProps}>{text}</GreyBtnComponent>
    )
  }
}

GreyBtn.propTypes = {
  text: PropTypes.string.isRequired
}


